.promo {
	max-width: 1280px;
	padding: 75px 70px;
	box-sizing: border-box;
	background-color: #073042;

	display: grid;
	grid-template-columns: 1fr auto;
}

.promo__image {
	width: 310px;
	height: 304px;
	grid-column-end: 3;
	grid-column-start: 2;
}

.promo__description {
	max-width: 640px;
	grid-row-start: 1;
	grid-column-end: 2;
	grid-column-start: 1;
}

.promo__description-title {
	margin: 0;
	color: #FFF;
	font-size: 50px;
	font-weight: 400;
	line-height: 1.16;
}

.promo__description-paragraph {
	color: #FFF;
	font-size: 13px;
	font-weight: 400;
	line-height: normal;

	opacity: 0.5;
	max-width: 310px;
	margin: 36px 0 146px 0;
}

.promo__navigate {
	color: #FFF;
	font-size: 12px;
	font-weight: 500;
	line-height: 1.33333;
	text-decoration: none;

	border: none;
	display: block;
	padding: 9px 19px;
	border-radius: 3px;
	box-sizing: border-box;
	background: transparent;
	border: 1px solid rgba(255, 255, 255, 0.6);
}

.promo__navigate:hover {
	opacity: 0.7;
	cursor: pointer;
}

@media screen and (max-width: 1024px) {
	.promo__description-title {
		font-size: 42px;
	}
}

@media screen and (max-width: 880px) {
	.promo__description-title {
		font-size: 34px;
	}

	.promo__image {
		width: 280px;
		height: 274px;
	}
}

@media screen and (max-width: 768px) {
	.promo {
		padding: 140px 70px 76px;

		display: flex;
		flex-direction: column;
	}

	.promo__image {
		width: 310px;
		height: 304px;
		margin: 0 auto 49px;
	}

	.promo__description-title {
		font-size: 40px;
		line-height: 1.25;
		text-align: center;
	}

	.promo__description-paragraph {
		max-width: 260px;
		text-align: center;
		margin: 22px auto 91px;
	}

	.promo__navigate {
		margin: 0 auto;
		padding: 9px 20px;
	}
}

@media screen and (max-width: 550px) {
	.promo {
		padding: 70px 10px 31px;
		box-sizing: border-box;
	}

	.promo__navigate {
		margin: 0 auto;
		padding: 9px 19px;
	}

	.promo__image {
		width: 210px;
		height: 206px;
		margin: 0 auto 44px;
	}

	.promo__description-title {
		font-size: 30px;
		text-align: center;
		line-height: 1.26667;
		letter-spacing: -1.2px;
	}

	.promo__description-paragraph {
		max-width: 260px;
		text-align: center;
		margin: 24px auto 80px;
	}
}

@media screen and (max-width: 550px) {
	.promo {
		padding: 70px 10px 31px;
		box-sizing: border-box;
	}

	.promo__navigate {
		margin: 0 auto;
		padding: 9px 19px;
	}

	.promo__image {
		width: 210px;
		height: 206px;
		margin: 0 auto 44px;
	}

	.promo__description-title {
		padding: 0 10px;
		font-size: 30px;
		text-align: center;
		line-height: 1.26667;
		letter-spacing: -1.2px;
	}

	.promo__description-paragraph {
		max-width: 260px;
		text-align: center;
		margin: 24px auto 80px;
	}
}